/**
Layers
**/
@import 'button.css';

/**
Base
**/
@tailwind base;
@tailwind components;
@tailwind utilities;

/**
Prefix
**/
.i2c-beta * {
    @apply font-sans;
}

/**
Filter (Custom style as ch32 existing style)
**/
.i2c-beta .filter-select {
    @apply relative block w-full p-0 bg-white cursor-pointer   #{!important};
}

.i2c-beta .filter-select label, .i2c-beta .filter-select .field label {
    @apply absolute w-full px-6 pt-4 text-gray-400 font-normal inset-0 text-xs #{!important};
}

.i2c-beta .filter-select .chosen-single,
.i2c-beta .filter-select input:not(.chosen-search-input),
.i2c-beta .filter-select .field input.ss-field,
.i2c-beta .filter-select select {
    z-index: 1; /* TODO */
    @apply relative px-6 pt-8 pb-4 m-0  inset-0 border border-0 bg-transparent rounded-none text-sm shadow-none outline-none  #{!important};
}

.i2c-beta .filter-select .chosen-container-active.chosen-with-drop .chosen-single {
    @apply border-violet-500 outline-none border-violet-700  #{!important};
}

.i2c-beta .filter-select .field input.ss-field ~ .link {
    @apply top-1/3 #{!important};
}

.i2c-beta .filter-select .field input.error-field {
    @apply border border-red-600 focus:border-red-600 outline-none outline-offset-0 focus:outline-red-200 #{!important};
}

/**
chosen
**/
.i2c-beta .chosen-container {
    @apply w-full relative  #{!important};
}

.i2c-beta .chosen-container .chosen-drop {
    display: none;
}

.i2c-beta .chosen-container.chosen-with-drop .chosen-drop {
    @apply block  #{!important};
}

.i2c-beta .chosen-container-active.chosen-with-drop .chosen-single {
    @apply border-violet-500 outline outline-1 outline-violet-700  #{!important};
}

.i2c-beta .chosen-drop {
    @apply absolute w-full border-0 border-transparent transition-none transform-none mt-2 rounded-md shadow-lg bg-white ring-1 ring-black ring-opacity-5 divide-y divide-gray-100 focus:outline-none z-20  #{!important};
}

.i2c-beta .chosen-single {
    @apply mt-1 block w-full py-2 px-3 border border-gray-300 bg-white rounded-lg text-sm shadow-sm cursor-pointer  #{!important};
}

.i2c-beta .chosen-results {
    @apply py-2 px-2  #{!important};
}

.i2c-beta .active-result {
    @apply block px-4 py-2 text-sm rounded-md text-gray-700 hover:bg-violet-50 hover:text-violet-700 cursor-pointer  #{!important};
}

.i2c-beta .chosen-container .chosen-results li.active-result.result-selected {
    @apply font-normal bg-violet-700 text-violet-50  #{!important};
}

.i2c-beta .chosen-container .chosen-results li.active-result.result-selected:after {
    @apply text-white;
}

.i2c-beta .chosen-label {
    @apply block text-sm font-medium text-gray-700  #{!important};
}

.i2c-beta .chosen-search {
    @apply py-2 px-2  #{!important};
}

.i2c-beta .chosen-search-input {
    @apply block bg-gray-200 w-full py-2 px-3 border border-transparent shadow-none rounded-md text-sm border-0 bg-none  #{!important};
}


/**
s:text
**/
.i2c-beta .field {
    @apply relative;
}

.i2c-beta input {
    @apply transition duration-150 ease-in-out #{!important};
}

.i2c-beta .field label {
    @apply block text-sm font-medium text-gray-700 top-0 transform-none transition-none #{!important};
}

.i2c-beta .field input.ss-field,
.i2c-beta .field textarea.ss-textarea {
    @apply py-2 px-3 text-gray-900 border h-auto mt-1 text-base focus:ring-indigo-500 focus:border-indigo-500 block w-full shadow-sm sm:text-sm border-gray-300 rounded-lg pr-14 #{!important};
}

.i2c-beta .field input.ss-field ~ .link {
    @apply text-gray-900 font-normal text-xs absolute top-1/4 text-gray-400 hover:text-violet-700 right-3 #{!important};
}

.i2c-beta .field input.error-field, .i2c-beta .field textarea.error-field {
    @apply border border-red-600 focus:border-red-600 outline-none outline-offset-0 focus:outline-red-200 #{!important};
}

/**
autocomplete
**/
.i2c-beta .ui-menu {
    @apply absolute bg-white shadow-lg py-2 px-2 border  border-gray-200 rounded rounded-lg;
}

.i2c-beta .ui-menu-item-wrapper {
    @apply block px-4 py-2 text-sm rounded-md text-gray-700 hover:bg-violet-50 hover:text-violet-700 cursor-pointer;
}

.i2c-beta .ui-menu .ui-state-active {
    @apply bg-violet-100 text-violet-700;
}

/**
Dialog Style
**/
.i2c-beta .ui-dialog {
    @apply bg-white shadow-xl rounded-xl z-101;
}

.i2c-beta .ui-widget-header {
    @apply relative;
}

.i2c-beta .ui-dialog-titlebar {
    @apply block px-6 pt-6;
}

.i2c-beta .ui-dialog-buttonpane {
    @apply px-6 pb-6 pt-2;
}

.i2c-beta .ui-dialog-content {
    @apply px-6 py-6;
}

.i2c-beta .ui-widget-overlay {
    @apply fixed inset-0 bg-gray-700 opacity-70;
}

.i2c-beta .ui-dialog-title {
    @apply text-gray-900 font-medium text-lg;
}

.i2c-beta .ui-dialog-content p {
    @apply text-gray-500 text-sm;
}

.i2c-beta .ui-dialog-titlebar-close {

    @apply absolute right-0 top-0 p-3;
}

/**
Date Picker Style
**/
.i2c-beta .ui-datepicker {
    @apply text-gray-700 p-0 w-auto mt-2 text-sm bg-white shadow-xl border  border-gray-200 rounded rounded-md #{!important};
}

.i2c-beta .ui-datepicker-group {
    @apply border border-b-gray-100 #{!important};
}

.i2c-beta .ui-datepicker-group:last-child {
    border: none #{!important};
}

.ui-widget-header .ui-icon {
    @apply bg-none;
}

.i2c-beta .ui-datepicker-header {
    @apply w-full bg-transparent shadow-none border-none flex font-medium justify-between items-center px-6 pt-6 pb-0 h-16 #{!important};
}

.i2c-beta .ui-datepicker-header.ui-helper-clearfix:before,
.i2c-beta .ui-datepicker-header.ui-helper-clearfix:after {
    @apply hidden  #{!important};
}

.i2c-beta .ui-datepicker-prev {
    order: theme('order.1');
}

.i2c-beta .ui-datepicker-next {
    order: theme('order.3');
}

.i2c-beta .ui-datepicker-prev,
.i2c-beta .ui-datepicker-next {
    content: ' ';
    @apply relative w-10 h-10 bg-transparent flex items-center top-auto justify-center cursor-pointer #{!important};
}

.i2c-beta .ui-datepicker-prev {
    @apply left-auto;
}

.i2c-beta .ui-datepicker-next {
    @apply right-auto;
}

.i2c-beta .ui-datepicker .ui-datepicker-header a {
    @apply text-gray-500 hover:text-violet-700 focus:text-violet-700 #{!important};
}

/*.i2c-beta .ui-datepicker .ui-icon  {
    @apply w-10 h-10 flex items-center justify-center #{!important};
}*/
.i2c-beta .ui-datepicker .ui-icon::before {
    @apply text-2xl #{!important};
}

.i2c-beta .ui-datepicker-multi .ui-datepicker-title {
    margin: 0 auto;
}

.i2c-beta .ui-datepicker-multi .ui-datepicker-buttonpane {
    @apply justify-end #{!important};
}

.i2c-beta .ui-datepicker-multi .ui-datepicker-buttonpane > button {
    width: auto;
    flex: none;
}

.i2c-beta .ui-datepicker-title {
    order: theme('order.2');
    @apply flex text-base rounded #{!important};
}

.i2c-beta .ui-datepicker-title .chosen-search {
    @apply hidden #{!important};
}

.i2c-beta .ui-datepicker-title .active-result {
    @apply px-0 text-center #{!important};
}

.i2c-beta .ui-datepicker-title .chosen-container-single {
    @apply w-full  #{!important};
}
.i2c-beta .ui-datepicker-title  .chosen-single {
    @apply m-0 #{!important};
}
.i2c-beta .ui-datepicker-calendar {
    border-collapse: inherit !important;
    border-spacing: unset !important;
    @apply w-full m-0 border-6 border-transparent #{!important};
}

.i2c-beta .ui-datepicker .ui-datepicker-calendar .ui-state-default {
    @apply bg-transparent  border-none;
}

.i2c-beta .ui-datepicker-calendar th {
    @apply border-0 text-sm font-medium py-2 #{!important};
}

.i2c-beta .ui-datepicker-calendar td {
    @apply text-sm p-0 font-normal text-center border-0 #{!important};
}

.i2c-beta .ui-datepicker-calendar td > a {
    @apply text-sm ;
}

.i2c-beta .ui-datepicker .ui-state-default {
    @apply w-10 h-10 inline-flex items-center justify-center snap-align-none #{!important};
}

.i2c-beta .ui-datepicker .ui-state-active {
    @apply bg-violet-600 text-white rounded-full #{!important};
}

.i2c-beta .ui-datepicker-today {
    @apply relative #{!important};
}

.i2c-beta .ui-datepicker-today > .ui-state-default {
    @apply relative bg-violet-100 text-violet-700 rounded-full font-medium #{!important};
}

.i2c-beta .ui-datepicker-today:after {
    content: ' ';
    @apply absolute w-1 h-1 inline-block rounded-full bg-violet-700 inset-x-1  bottom-1 m-auto #{!important};
}

.i2c-beta .ui-datepicker-calendar .ui-state-hover {
    @apply relative bg-violet-100 text-violet-700 rounded-full font-medium #{!important};
}

.i2c-beta .ui-datepicker-buttonpane {
    @apply flex justify-between items-center m-0 px-6 py-6 border-t border-t-gray-200 #{!important};
}

.i2c-beta .ui-datepicker-buttonpane > button {
    @apply flex-1 #{!important};
}

.i2c-beta .ui-datepicker-current {
    @apply mr-2 h-10 inline-flex shadow-xs border border-gray-300 items-center justify-center px-5 py-3 text-sm font-medium rounded-lg text-gray-700 hover:bg-gray-50 bg-white focus:outline-none focus:ring-4 focus:ring-gray-100 #{!important};
}

.i2c-beta .ui-datepicker-close {
    @apply ml-2 h-10 inline-flex shadow-xs items-center justify-center px-5 py-3 text-sm font-medium rounded-lg border border-violet-700 text-white hover:bg-violet-600 bg-violet-700 focus:outline-none focus:ring-4 focus:ring-violet-100 #{!important};
}

.i2c-beta .ui-datepicker-multi {
    @apply w-auto #{!important};
}
